  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: end;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

header .menu {
  width: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: end;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

header .menu a {
  position: relative;
}

header .menu a::after {
  content: '';
  width: 120px;
  height: 12px;
  background-color: #41FF08;
  display: block;
  position: absolute;
  top: 140;
  left: calc(50% - 60px);
  bottom: -70;
  right: 0;
  z-index: -100;
}

header .menu a:hover::after {
  top: 70;
  bottom: 0;
}

header .menu a.active::after {
  content: '';
  width: 120px;
  height: 12px;
  background-color: #41FF08;
  display: block;
  position: absolute;
  top: 70;
  left: calc(50% - 60px);
  bottom: 0;
  right: 0;
  z-index: -1;
}

