@import '_common.scss';

main{
    position: relative;
}
.home-content1{
    height: calc(100vh - 160px);
    position: relative;
    a span{
        color: $sc;
        font-size: 24px;
        position: absolute;
        right: 20px; top: 20vh;
    }
    a:hover span{
        text-decoration: underline;
    }
    .main-title{
        padding-top: 24vh;
        span, p span{
            color: $st;
            font-size: 80px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        h3 {
            height: 140px;
            display: inline-block;
            overflow: hidden;
            transform: translateY(24px);
            p{
            color: $mc;
            font-size: 116px;
            font-weight: 700;
            letter-spacing: 0.04em;
            animation: text 8s infinite;
            }
        }
        p{
            font-size: 116px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        sup{
            color: $sc;
            font-size: 60px;
            font-weight: 400;
        }
    }
}
// 메인텍스트 돌아가는 애니메이션
@keyframes text{
    0%, 20%{ transform:translateY(0) }
    30%,50%{ transform:translateY(-100%) }
    60%,80%{ transform:translateY(-200%) }
    100%{ transform:translateY(-300%) }
}
// 스크롤 아이콘 움직이는 애니메이션
@keyframes scroll{
    0% {transform: translateY(0px);}
    50% {transform: translateY(-80px);}
    100% {transform: translateY(0px);}
}

// ================================================================================
// ============================= about ============================================
// ================================================================================

.cv{
    position: relative;
    .scroll{
        position: absolute;
        left: 80px; bottom: -160px;
        img{
            animation: scroll 3s ease infinite;
        }
    }
    .cv-name{
        position: absolute;
        left: 128px; top: 0;
        p{
            font-size: 80px;
            font-weight: 700;
            letter-spacing: 0.04em;
        }
        sup{
            color: $sc;
            font-size: 40px;
            font-weight: 700;
            letter-spacing: 0;
            line-height: 100px;
        }
        p:nth-of-type(2){
            margin-left: 32px;
            color: $mc;
            font-size: 60px;
        }
    }
    .cv-profile{
        width: 848px;
        margin: 180px 0 292px 432px;
    }
    .cv-email{
        position: absolute;
        right: 38px; bottom: -84px;
        p{
            font-family: Arial Black,Arial Bold,Gadget,sans-serif;
            font-size: 24px;
            font-weight: 700;
            letter-spacing: 0.02em;
            line-height: 1.4;
        }
        p:nth-of-type(1){
            color: $st;
        }
        p:nth-of-type(2){
            text-transform: lowercase;
        }
        &::before{
            content: '';
            height: 54px;
            border-right: 4px solid $sc;
            position: absolute;
            right: 288px; bottom: 6px;
        }
    }
}
.slide-ani{
    div{
        height: 80px;
        margin-bottom: 20px;
        border-top: 1px solid $st;
        border-bottom: 1px solid $st;
        overflow: hidden;
    }
    p{
        margin-top: 8px;
        display: inline-block;
        white-space: nowrap;
        font-size: 48px;
        font-weight: 700;
        color: $st;
    }
    i{
        font-size: 48px;
        font-weight: 700;
        color: $mc;
    }
    b{
        font-size: 48px;
        font-weight: 700;
        color: $sc;
    }
    .slide1{
        animation: slide1 20s linear infinite;
    }
    @keyframes slide1 {
        from   { transform: translate(0%);}
        99%,to { transform: translate(-20%);}
    }
    .slide2{
        animation: slide2 20s linear infinite;
    }
    @keyframes slide2 {
        from   { transform: translate(-20%);}
        99%,to { transform: translate(0%);}
    }
}
.cv-text{
    margin: 240px 0 120px;
    p{
        text-align: center;
    }
    p img{
        width: 92px;
    }
    .cv-title{
        margin: 80px 0;
        font-size: 24px;
        font-weight: 100;
        letter-spacing: 0.02em;
        b{
            font-size: 24px;
            font-weight: 400;
            letter-spacing: 0.02em;
        }
    }
    .cv-letter{
        line-height: 1.8;
        letter-spacing: 0.02em;
        position: relative;
        &::after{
            content: '';
            width: 80px;
            border: 4px solid $sc;
            position: absolute;
            left: calc(50% - 40px); bottom: -120px;
        }
    }
}
.education{
    margin: 240px 0;
    text-align: center;
}
.skill{
    text-align: center;
    h4{
        margin: 80px 0;
    }
    p img{width: 92px;}
}

// ================================================================================
// ============================= contact ==========================================
// ================================================================================

.contact{
    width: 320px; height: calc(100vh - 160px);
    margin: 0 auto;
    padding-top: 160px;
    form{
        margin-top: 40px;
    }
    p{
        margin: 20px 0;
        display: flex;
        justify-content: space-between;
    }
    label{
        font-family: Arial, Helvetica, sans-serif;
        font-size: 20px;
    }
    p:nth-of-type(1) label,
    p:nth-of-type(2) label {
        &::after{
            content: '*';
            margin: 0 0 8px 4px;
            color: $mc;
            font-weight: 700;
        }
    }
    input{
        width: 240px;
        border-bottom: 2px solid $sc;
        text-transform: none;
    }
    p:nth-of-type(3){
        display: block;
        label{
            &::after{
                content: '1000자까지 / *표시는 필수입력란 입니다.';
                margin-left: 18px;
                color: $mc;
                font-size: 12px;
                font-weight: 700;
            }
        }
    }
    textarea{
        width: 320px; height: 240px;
        margin-top: 12px;
        padding: 4px;
        border: 2px solid $sc;
        text-transform: none;
    }
    p:nth-of-type(4){
        justify-content: flex-end;
    }
    button{
        width: 120px; height: 28px;
        background-color: $mc;
        color: #fff;
        font-family: Arial Black,Arial Bold,Gadget,sans-serif;
        font-size: 20px;
        font-weight: 400;
    }
}

.thanks{
    height: calc(100vh - 160px);
    padding: 360px 0;
    text-align: center;
    h4{
        
        font-size: 32px;
        &:hover{
            color: $mc;
        }
    }
    h4:hover+p{
        display: block;
        color: $sc;
    }
    p{
        color: $mc;
        font-family: Arial, Helvetica, sans-serif;
        text-transform: none;
    }
}